.network-top {
  padding-bottom: 0;
}
.network-top .tit {
  font-size: 0.22rem;
  color: #040E34;
  line-height: 1;
  margin: 0.33rem 0.3rem 0.13rem 0.3rem;
}
.network-top .tabs {
  border-bottom: 1px solid #D9DBE1;
  padding: 0 0.1rem;
  margin-bottom: 0.3rem;
}
.network-top .tabs a {
  padding: 0.17rem 0;
  display: inline-block;
  margin: 0 0.2rem;
  cursor: pointer;
  position: relative;
  line-height: 1;
  font-size: 0.16rem;
  color: #525666;
}
.network-top .tabs a::after {
  content: '';
  width: 0;
  height: 2px;
  background: #5C5EE4;
  bottom: 0;
  left: 50%;
  position: absolute;
  transition: all 0.2s;
}
.network-top .tabs a.active {
  color: #040E34;
}
.network-top .tabs a.active::after {
  width: 100%;
  left: 0;
  bottom: -1px;
}
.ant-table-tbody > tr > td {
  height: 0.6rem;
}
